.container {
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

.searchBar {
  display: flex;
  gap: 5px;
  margin: 20px 0;
}

.searchBar input {
  height: 25px;
  width: 200px;
  border: none;
  border-radius: 2px;
}

.searchBar button {
  height: 27px;
  width: 100px;
  border: none;
  border-radius: 2px;
  background-color: rgb(1, 134, 34);
  color: white;
}

.clearButton {
  height: 27px;
  width: 170px;
  border: none;
  border-radius: 2px;
  margin: 20px 0;
  background-color: rgb(212, 88, 30);
  color: white;
}
